import { useState, useEffect } from 'react';

export const useDarkMode = () => {
  const isDarkModeSaved = localStorage.getItem('darkMode') === 'true';
  const [darkMode, setDarkMode] = useState(isDarkModeSaved);

  const toggleDarkMode = () => {
    setDarkMode(prevDarkMode => !prevDarkMode);
  };

  useEffect(() => {
    // Zapisz warto trybu ciemnego w localStorage po zmianie
    localStorage.setItem('darkMode', darkMode);

    // Zaktualizuj klasy CSS w dokumencie na podstawie wartoci darkMode
    document.documentElement.classList.toggle('dark-mode', darkMode);

    // Dostosuj klasy CSS dla elementw poza kontenerem, ktre maj zmienia si w trybie jasnym
    const elementsToToggle = document.querySelectorAll('.dark-mode');
    elementsToToggle.forEach(element => {
      element.classList.toggle('dark-mode', darkMode);
    });
  }, [darkMode]);

  return { darkMode, toggleDarkMode };
};
